import React from "react";
import { Hidden } from "@material-ui/core";
import BrandLogo from "../../../Container/AllPayments/Brandlogo/Brandlogo";

import {
  Divider,
  Link,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
} from "@mui/material/";

import { withStyles } from "@material-ui/core";
//import { useBusinessProfile } from "../../contexts/BusinessProfileContext";
import { useBusinessProfile } from "../../../contexts/BusinessProfileContext";

import useStyles from "./styles/muiStyles";

import Reload from "./icons/reload";
import Copy from "./icons/copy";
import Wallet from "./icons/wallet";
import Invoices from "./icons/invoices";
import Refunds from "./icons/refunds";

const StyledListItem = withStyles({
  borderRadius: "12px",
  cursor: "pointer",
  root: {
    marginBottom: "6px",
    width: "100%",
    height: "42px",

    "&:hover": {
      backgroundColor: "rgba(159, 159, 159, 0.1)!important",

      "& svg": {
        "& path": {
          fill: "#464A51",
        },
      },

      "& .MuiListItemText-primary": {
        color: "#464A51",
      },
    },

    "&.Mui-selected": {
      backgroundColor: "#32c5ff1a!important",

      "& svg": {
        "& path": {
          fill: "#464A51",
        },
      },

      "& .MuiListItemText-primary": {
        color: "#464A51",
      },
    },

    "& .MuiListItemIcon-root": {
      minWidth: "30px",
    },

    "& svg": {
      "& path": {
        fill: "#686C72",
      },
    },

    "& .MuiListItemText-primary": {
      color: "#686C72",
      fontSize: "15px!important",
      fontWeight: 500,
      lineHeight: "18px!important",
    },
  },
})(ListItem);

const NewMobileSettings = () => {
  const classes = useStyles();
  const { selectedBusiness } = useBusinessProfile();

  const exitSetting = () => {
    const redirectTo = selectedBusiness?.redirectUrl
      ? selectedBusiness.redirectUrl
      : "/engage/chat";
    window.location.href = redirectTo;
  };

  return (
    <Hidden smUp implementation="js">
      <div className={classes.wrap}>
        <div className="head">
          <div className="above">
            <BrandLogo />
            <h4 className="page-title">{"BUSINESS SETTINGS"}</h4>
          </div>
          <div className="below">
            <div className="breadcrumb">
              <Link className="root" href="/all-payments" underline="hover">
                {"All Payments"}
              </Link>
              <span className="separator">{" / "}</span>
              <span className="after">{"Order History"}</span>
            </div>
          </div>
        </div>
        <Divider variant="fullWidth" style={{ color: "#24272c1a" }} />
        <List className="menu">
          <Link
            underline="none"
            color="inherit"
            href="/all-payments/subscriptions"
            aria-label="Subscriptions"
          >
            <StyledListItem
              selected={window.location.pathname.includes("subscriptions")}
            >
              <ListItemIcon>
                <Reload />
              </ListItemIcon>
              <ListItemText primary="Subscriptions" />
            </StyledListItem>
          </Link>
          <Link
            underline="none"
            color="inherit"
            href="/all-payments/orders"
            aria-label="Orders"
          >
            <StyledListItem
              selected={window.location.pathname.includes("orders")}
            >
              <ListItemIcon>
                <Copy />
              </ListItemIcon>
              <ListItemText primary="Orders" />
            </StyledListItem>
          </Link>
          <Link
            underline="none"
            color="inherit"
            href="/all-payments/wallet"
            aria-label="Wallet"
          >
            <StyledListItem
              selected={window.location.pathname.includes("wallet")}
            >
              <ListItemIcon>
                <Wallet />
              </ListItemIcon>
              <ListItemText primary="Wallet" />
            </StyledListItem>
          </Link>
          <Link
            underline="none"
            color="inherit"
            href="/all-payments/invoices"
            aria-label="Invoices"
          >
            <StyledListItem
              selected={window.location.pathname.includes("invoices")}
            >
              <ListItemIcon>
                <Invoices />
              </ListItemIcon>
              <ListItemText primary="Invoices" />
            </StyledListItem>
          </Link>
          {false && (
            <Link
              underline="none"
              color="inherit"
              href="/all-payments/refunds"
              aria-label="Refunds"
            >
              <StyledListItem
                selected={window.location.pathname.includes("refunds")}
              >
                <ListItemIcon>
                  <Refunds />
                </ListItemIcon>
                <ListItemText primary="Refunds" />
              </StyledListItem>
            </Link>
          )}
        </List>
        <div className="bottom">
          <Divider variant="fullWidth" style={{ color: "#24272c1a" }} />
          <div className="actions">
            {false && (
              <Link underline="none" color="inherit" className="payment">
                {"All Payments"}
              </Link>
            )}
            <Link
              underline="none"
              color="inherit"
              className="exit"
              onClick={() => exitSetting()}
            >
              {"Exit Settings"}
            </Link>
          </div>
        </div>
      </div>
    </Hidden>
  );
};

export default NewMobileSettings;
